.footer .footer__content-footer-input:focus{
            outline: none;
        }





.footer .footer__content-footer-input--white{
            background-color: #f4f4f4;
            border-bottom: #252422 solid 1.5px;
        }





.footer .footer__content-footer-input--white::-moz-placeholder{
            color: #252422;
        }





.footer .footer__content-footer-input--white::placeholder{
            color: #252422;
        }





.footer .footer__content-footer-button{
        background-color: #252422;
        border: none;
        position: absolute;
        top: 0;
        right: 5px;
    }





.footer .footer__content-footer-button--white{
            background-color: #f4f4f4;
        }





.footer .footer__content-footer-button-svg{
        fill: #f4f4f4;
        width: 20px;
        height: 20px;
    }





.footer .footer__content-footer-button-svg--black{
            fill: #252422;
        }





.footer .footer__copyright{
        width: 100%;
        background-color: rgb(28, 26, 26);
        color: #828282;
    }





.footer .footer__copyright--white{
            background-color: #e5e5e5;
        }





.footer .footer__copyright-container{
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
    }





.footer .footer__copyright-text{
        font-size: 0.7rem;
    }






@keyframes hero-animation{
    0%{
        top: 0;
    }
    100%{
        top: -80px;
    }
}


@keyframes button-animation{
    0%{
        color: #252422;
        background-position: -100% 0;
    }
    100%{
        color: #f4f4f4f4;
        background-position: 0 100%;
    }
}
@keyframes button-animation-off{
    0%{
        color: #f4f4f4f4;
        background-position: 0 100%;
    }
    100%{
        color: #252422;
        background-position: -100% 0;
    }
}



@keyframes button-animation-pink{
    0%{
        color: #f9cdcd;
        background-position: -100% 0;
    }
    100%{
        color: #252422;
        background-position: 0 100%;
    }
}
@keyframes button-animation-pink-off{
    0%{
        color: #252422;
        background-position: 0 100%;
    }
    100%{
        color: #f9cdcd;
        background-position: -100% 0;
    }
}




