;\r\n    margin-top: 70px;\r\n    animation: hero-animation 0.5s 1 ease-in-out forwards;\r\n    &--delay-1{\r\n        animation-delay: 0.2s;\r\n    }\r\n    &--delay-2{\r\n        animation-delay: 0.4s;\r\n    }\r\n    &--delay-3{\r\n        animation-delay: 0.6s;\r\n    }\r\n    &--delay-4{\r\n        animation-delay: 0.8s;\r\n    }\r\n}\r\n\r\n\r\n\r\n.header{\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 2;\r\n    width: 100%;\r\n    height: 130px;\r\n    .header__container{\r\n        width: 90%;\r\n        margin: 0 auto;\r\n        height: 100%;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: space-between;\r\n    }\r\n    .header__logo{\r\n        width: 160px;\r\n        fill: #f4f4f4;\r\n        &--black{\r\n            fill: #252422;\r\n        }\r\n        &--pink{\r\n            fill: #f9cdcd;\r\n        }\r\n    }\r\n    .header__nav{\r\n        display: flex;\r\n        padding-right: 100px;\r\n    }\r\n    .header__nav-item{\r\n        color: #f4f4f4;\r\n        text-decoration: none;\r\n        margin: 0 2rem;\r\n        position: relative;\r\n        &:after{\r\n            height: 1px;\r\n            position: absolute;\r\n            content: '';\r\n            bottom: 0;\r\n            left: 0;\r\n            background-color: #f4f4f4;\r\n            width: 0;\r\n            transition: all 0.35s ease;\r\n        }\r\n        &:hover:after{\r\n            width: 100%;\r\n        }\r\n    }\r\n    .header__nav-item--current:after{\r\n        width: 100%;\r\n    }\r\n    .header__nav-item--black{\r\n        color: black;\r\n        &:after{\r\n            background-color: #252422;\r\n        }\r\n    }\r\n    .header__nav-item--pink{\r\n        color: #f9cdcd;\r\n        &:after{\r\n            background-color: #f9cdcd;\r\n        }\r\n    }\r\n    .header__menu{\r\n        background: inherit;\r\n        border: none;\r\n        color: #f4f4f4;\r\n        font-size: 1.3rem;\r\n        cursor: pointer;\r\n    }\r\n    .header__menu--black{\r\n        color: #252422;\r\n    }\r\n    .header__menu--pink{\r\n        color: #f9cdcd;\r\n    }\r\n}\r\n\r\n\r\n.accessibility{\r\n    box-sizing: content-box;\r\n    position: fixed;\r\n    bottom: 20px;\r\n    left: 20px;\r\n    z-index: 2;\r\n    background-color: #252422;\r\n    width: 30px;\r\n    height: 30px;\r\n    border-radius: 50%;\r\n    border: none;\r\n    padding: 8px;\r\n    cursor: pointer;\r\n    &:hover{\r\n        transform: scale(1.1);\r\n        transition: all 0.2s ease-in-out;\r\n    }\r\n    &:not(hover){\r\n        transform: scale(1);\r\n        transition: all 0.2s ease-in-out;\r\n    }\r\n    .accessibility__icon{\r\n        width: 100%;\r\n        height: 100%;\r\n        fill: #f4f4f4;\r\n    }\r\n}\r\n\r\n\r\n.hero{\r\n    width: 100%;\r\n    height: 100vh;\r\n    color: #252422;\r\n    &--height-auto{\r\n        height: auto;\r\n    }\r\n    &--black{\r\n        background-color: #252422;\r\n        color: #f9cdcd;\r\n    }\r\n    &__container{\r\n        width: 90%;\r\n        height: 100%;\r\n        margin: 0 auto;\r\n    }\r\n    &__content{\r\n        width: 100%;\r\n        padding: 170px 0;\r\n    }\r\n    &__title{\r\n        font-size: 6rem;\r\n        font-weight: 700;\r\n        line-height: 5.5rem;\r\n    }\r\n    &__content-text{\r\n        display: inline-block;\r\n        width: 450px;\r\n        margin-top: 50px;\r\n        margin-right: 10px;\r\n    }\r\n    &__content-text-p{\r\n        margin-top: 20px;\r\n        font-size: 1.2rem;\r\n        font-weight: 500;\r\n    }\r\n    &__footer{\r\n        width: 100%;\r\n        display: flex;\r\n        flex-direction: row-reverse;\r\n        justify-content: space-between;\r\n        align-items: end;\r\n        flex-wrap: wrap;\r\n    }\r\n    &__text{\r\n        width: 380px;\r\n        font-weight: 500;\r\n        font-size: 1.1rem;\r\n        line-height: 1.5rem;\r\n        margin-right: 60px;\r\n    }\r\n    &__nav{\r\n        display: flex;\r\n    }\r\n    &__nav-item{\r\n        color: #f4f4f4;\r\n        text-decoration: none;\r\n        margin-right: 3rem;\r\n        position: relative;\r\n        &:after{\r\n            height: 1px;\r\n            position: absolute;\r\n            content: '';\r\n            bottom: 0;\r\n            left: 0;\r\n            background-color: #f4f4f4;\r\n            width: 0;\r\n            transition: all 0.35s ease;\r\n        }\r\n        &:hover:after{\r\n            width: 100%;\r\n        }\r\n        &--current:after{\r\n            width: 100%;\r\n        }\r\n        &--black{\r\n            color: #252422;\r\n            &:after{\r\n                background-color: #252422;\r\n            }\r\n        }\r\n        &--pink{\r\n            color: #f9cdcd;\r\n            &:after{\r\n                background-color: #f9cdcd;\r\n            }\r\n        }\r\n    }\r\n    &__circle-nav{\r\n        display: flex;\r\n        float: right;\r\n    }\r\n    &__circle-nav-item:last-child{\r\n        margin-right: 0;\r\n    }\r\n    &__circle-nav-item{\r\n        font-weight: 600;\r\n        font-size: 0.9rem;\r\n        color: #f4f4f4;\r\n        text-decoration: none;\r\n        padding-left: 10px;\r\n        margin-right: 70px;\r\n        position: relative;\r\n        &:before{\r\n            box-sizing: border-box;\r\n            height: 20px;\r\n            width: 20px;\r\n            position: absolute;\r\n            content: '';\r\n            top: -2px;\r\n            left: -20px;\r\n            background-color: #f4f4f4;\r\n            border: #252422 solid 1px;\r\n            border-radius: 50%;\r\n        }\r\n        &:after{\r\n            box-sizing: border-box;\r\n            height: 0px;\r\n            width: 0px;\r\n            position: absolute;\r\n            content: '';\r\n            top: 7px;\r\n            left: -10px;\r\n            opacity: 0;\r\n            background-color: #252422;\r\n            border-radius: 50%;\r\n            transition: all 0.35s ease;\r\n        }\r\n        &:hover:after{\r\n            width: 10px;\r\n            height: 10px;\r\n            top: 3px;\r\n            left: -15px;\r\n            opacity: 1;\r\n        }\r\n        &--current:after{\r\n            width: 20px;\r\n            height: 20px;\r\n            top: -2px;\r\n            left: -20px;\r\n            opacity: 1;\r\n        }\r\n        &--black{\r\n            color: #252422;\r\n            &:before{\r\n                background-color: #f4f4f4;\r\n            }\r\n            &:after{\r\n                background-color: #252422;\r\n            }\r\n        }\r\n        &--pink{\r\n            color: #f9cdcd;\r\n            &:before{\r\n                background-color: #252422;\r\n                border: #f9cdcd solid 1px;\r\n            }\r\n            &:after{\r\n                background-color: #f9cdcd;\r\n            }\r\n        }\r\n    }\r\n    &__video{\r\n        width: 100%;\r\n        height: 100%;\r\n        object-fit: cover;\r\n    }\r\n}\r\n\r\n\r\n.awards{\r\n    width: 100%;\r\n    height: 70vh;\r\n    .awards__container{\r\n        border-bottom: #252422 solid 1px;\r\n        width: 90%;\r\n        height: 100%;\r\n        margin: 0 auto;\r\n        display: flex;\r\n        justify-content: space-around;\r\n        align-items: center;\r\n    }\r\n    .awards__link{\r\n        text-decoration: none;\r\n        color: #252422;\r\n        width: 33.3%;\r\n        text-align: center;\r\n        height: 40%;\r\n        &:hover{\r\n            .awards__logo{\r\n                transform: scale(1.05);\r\n                transition: all 0.3s ease-in-out;\r\n            }\r\n            .awards__title{\r\n                text-decoration: underline #252422 2px;\r\n            }\r\n        }\r\n        &:not(hover){\r\n            .awards__logo{\r\n                transform: scale(1);\r\n                transition: all 0.3s ease-in-out;\r\n            }\r\n        }\r\n    }\r\n    .awards__logo{\r\n        display: block;\r\n        margin: 10% auto;\r\n        width: 40%;\r\n        height: 40%;\r\n    }\r\n    .awards__title{\r\n        display: block;\r\n        width: 40%;\r\n        margin: 0 auto;\r\n        line-height: 1.2rem;\r\n        text-transform: uppercase;\r\n        font-size: 0.8rem;\r\n        font-weight: 500;\r\n    }\r\n}\r\n\r\n\r\n\r\n.overview{\r\n    width: 100%;\r\n    height: 50vh;\r\n    .overview__container{\r\n        width: 90%;\r\n        height: 100%;\r\n        margin: 0 auto;\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: start;\r\n    }\r\n    .overview__content{\r\n        width: 50%;\r\n        height: 100%;\r\n        padding: 80px 0;\r\n        padding-right: 80px;\r\n    }\r\n    .overview__link{\r\n        display: inline-block;\r\n        text-decoration: none;\r\n        color: #252422;\r\n        border: #252422 1px solid;\r\n        border-radius: 20px;\r\n        padding: 8px 30px;\r\n        margin-top: 40px;\r\n        font-size: 0.7rem;\r\n        font-weight: 600;\r\n        background-image: linear-gradient(to top, #252422 50%, #f4f4f4 50%);\r\n        background-size: 100% 200%;\r\n        &:hover{\r\n            animation: button-animation 0.25s 1 ease-in-out forwards;\r\n            \r\n        }\r\n        &:not(hover){\r\n            animation: button-animation-off 0.25s 1 ease-in-out forwards;\r\n        }\r\n    }\r\n    .overview__text{\r\n        font-size: 2.2rem;\r\n        font-weight: 500;\r\n        line-height: 2.5rem;\r\n    }\r\n    .overview__image{\r\n        width: 550px;\r\n        height: 350px;\r\n        padding-top: 80px;\r\n    }\r\n    .overview__image-svg{\r\n        fill: #252422;\r\n    }\r\n}\r\n\r\n\r\n\r\n\r\n.case-studies{\r\n    width: 100%;\r\n    height: calc(width);\r\n    margin-bottom: 10em;\r\n    .case-studies__container{\r\n        width: 90%;\r\n        height: 100%;\r